import React, { useEffect, useRef } from 'react'
import { Container } from 'react-bootstrap'
import "./Header.scss";

import Typical from 'react-typical'
import { TweenMax, Power3 } from "gsap";
export default function Landing() {
    let MainText = useRef(null);

    useEffect(() => {
        TweenMax.to(MainText, 1, { opacity: 2, x: 0, ease: Power3.easeOut })
    })
    return (

        <div>
            <div
                className="page-header section-dark"
                style={{
                    backgroundImage:
                        "url(" + require("../Images/abc.jpg") + ")"
                }}
            >
                <div className="filter" />
                <div className="content-bottom-right">
                    <Container>
                        <div ref={el => { MainText = el }}>
                            <div className="title-brand">
                                <h1 className="presentation-title" style={{ fontSize: "120px"  ,}}> SIDDHARTH<br /> MISHRA </h1>

                            </div>
                            <h2 className="presentation-subtitle text-bottom-right ">
                                <Typical
                                    steps={['Front End Developer', 800, "Content Creator", 800, 'Tech Enthusiast', 800, 100]}
                                    loop={Infinity}
                                    wrapper="p"
                                />
                            </h2>
                        </div>
                    </Container>
                </div>
            </div>
        </div>
    )
}
