import React, { useRef, useEffect } from 'react'
import { Navbar, Nav } from 'react-bootstrap'
import { TweenMax, gsap } from 'gsap/gsap-core';
export default function NavbarC() {

    let scroll1 = useRef(null);

    const scrollToMyCode = () => {
        window.scroll({
            top: 1300,
            behavior: 'smooth'
        })
    }
    return (

        <Navbar fixed="top" bg="black" variant="dark">

            <Nav className="mr-auto">
                <Nav.Link href="#home"
                    onClick={scrollToMyCode}>Home

                </Nav.Link>
                <Nav.Link href="#features">Work</Nav.Link>
                <Nav.Link href="#pricing" >Contact</Nav.Link>
            </Nav>
        </Navbar>

    )
}
